<template>
  <div id="appContainer">
    <!-- Sidebar with logo and navigation -->
    <div id="sidebar">
      <div id="logo">
        <img src="../assets/rio-logo.png" alt="Logo" />
      </div>
      <div id="preview-thumbnails">
        <button class="thumbnail" @click="viewUp">View 1</button>
        <button class="thumbnail" @click="viewDown">View 2</button>
        <button class="thumbnail" @click="viewLeft">View 3</button>
        <button class="thumbnail" @click="viewRight">View 4</button>
      </div>
    </div>

    <!-- Canvas area -->
    <div id="canvasArea">
      <canvas id="mcanvas"></canvas>
    </div>

    <!-- Control panel -->
    <div id="controlsPanel">
      <div class="control-group">
        <span class="group-label">Diamond Type</span>
        <div class="button-group">
          <button class="object ui-button" data-category="Basket" data-index="0">Cushion Halo</button>
          <button class="object ui-button" data-category="Basket" data-index="1">Empty</button>
          <button class="object ui-button" data-category="Basket" data-index="2">Pear Halo</button>
        </div>
      </div>
      <div class="control-group">
        <span class="group-label">Ring Type</span>
        <div class="button-group">
          <button class="object ui-button" data-category="Ring" data-index="0">Alliance</button>
          <button class="object ui-button" data-category="Ring" data-index="1">Baguette</button>
        </div>
      </div>
      <div class="control-group">
        <span class="group-label">Metal</span>
        <div class="button-group">
          <button class="material ui-button" data-category="Metal" data-index="0">Rose</button>
          <button class="material ui-button" data-category="Metal" data-index="1">White</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  ViewerApp,
  addBasePlugins,
  VariationConfiguratorPlugin,
  DiamondPlugin,
  CameraView,
  Vector3,
} from "webgi";

export default {
  name: "WebgiViewer",
  data() {
    return {
      viewer: null,
      controls: null, // Store camera controls
    };
  },
  mounted() {
    this.setupViewer();
  },
  methods: {
    async setupViewer() {
      this.viewer = new ViewerApp({
        canvas: this.$el.querySelector("#mcanvas"),
      });

      await addBasePlugins(this.viewer);
      await this.viewer.addPlugin(DiamondPlugin);
      const config = await this.viewer.addPlugin(VariationConfiguratorPlugin);
      this.camViewPlugin = this.viewer.plugins.CameraViews;
      this.viewer.renderer.refreshPipeline();
      this.controls = this.viewer.scene.activeCamera.controls;
      this.view = new CameraView();

      console.log('d',this.controls);

      await this.viewer.load(
        "https://3dassetsmaan.s3.ap-south-1.amazonaws.com/VariationConfiguratorTutorials/1/scene.vjson"
      );

      await config.importPath(
        "https://3dassetsmaan.s3.ap-south-1.amazonaws.com/VariationConfiguratorTutorials/1/config.json"
      );

      document.querySelectorAll(".object").forEach((el) => {
        const index = parseInt(el.getAttribute("data-index"));
        const category = config.variations.objects.find(
          (cat) => cat.name === el.getAttribute("data-category")
        );
        const type = "objects";
        el.addEventListener("click", async () => {
          await config.applyVariation(category, index, type);
        });
        if (index === 0) el.click();
      });

      document.querySelectorAll(".material").forEach((el) => {
        const index = parseInt(el.getAttribute("data-index"));
        const category = config.variations.materials.find(
          (cat) => cat.name === el.getAttribute("data-category")
        );
        const type = "materials";
        el.addEventListener("click", async () => {
          await config.applyVariation(category, index, type);
        });
        if (index === 0) el.click();
      });


      this.controls.autoRotate = true; // Enable auto-rotation
      this.controls.autoRotateSpeed = 2; // Set auto-rotation speed

    },

    

    // Camera view functions
    async viewUp() {

        this.controls.autoRotate = false; // Stop auto-rotation
        this.view.position = new Vector3(7, 8, 7);
        this.view.up = new Vector3(1, 1, 1);  // Assuming the up direction is (0,1,0)
        this.camViewPlugin.camViews.push(this.view);
        await this.camViewPlugin.animateToView(this.view, 500); // 2000 ms is the duration
        this.viewer.renderer.refreshPipeline();
      
    },
    async viewDown() {
      if (this.controls) {
        this.controls.autoRotate = false; // Stop auto-rotation
   
        this.view.position = new Vector3(-13, 1, 1);
        this.view.up = new Vector3(1, 1, 1);  // Assuming the up direction is (0,1,0)
        this.camViewPlugin.camViews.push(this.view);
        
        await this.camViewPlugin.animateToView(this.view, 500); // 2000 ms is the duration
        this.viewer.renderer.refreshPipeline();
      }
    },
    async viewLeft() {
      if (this.controls) {
        this.controls.autoRotate = false; // Stop auto-rotation
        this.view.position = new Vector3(0, 13, 0);
        this.view.up = new Vector3(1, 1, 1);  // Assuming the up direction is (0,1,0)
        this.camViewPlugin.camViews.push(this.view);
        await this.camViewPlugin.animateToView(this.view, 500); // 2000 ms is the duration
        this.viewer.renderer.refreshPipeline();
      }
    },
    async viewRight() {
      if (this.controls) {
        this.controls.autoRotate = false; // Stop auto-rotation
        this.view.position = new Vector3(-5, 2, 12);
        this.view.up = new Vector3(1, 1, 1);  // Assuming the up direction is (0,1,0)
        this.camViewPlugin.camViews.push(this.view);
        await this.camViewPlugin.animateToView(this.view, 500); // 2000 ms is the duration
        this.viewer.renderer.refreshPipeline();
      }
    },
  },
};
</script>

